/*tabs  */


.nav-tabs
 {
   
   border-color:rgb(147, 175, 184); /*#1A3E5E;*/
   width:60%;
   
 }
 


  
    .nav-tabs .nav-link.active,
    .nav-tabs .nav-link.active > a:focus,
    .nav-tabs .nav-link.active > a:hover{
      background-color:rgb(206, 247, 247); /*white */
   /*   background-color:rgb(147, 175, 184); */
   
    color:#000;
    border: 1px solid rgb(147, 175, 184);/*#1A3E5E;*/
    border-bottom-color: transparent;
    }

.nav-tabs > li > a:hover{
  background-color:rgb(206, 247, 247);
/*  background-color: rgb(173, 206, 221) !important; */
    border-radius: 5px;
    color:#000;

} 

.tab-pane {
    border:solid 1px rgb(216, 240, 245);/* #1A3E5E;*/
